/* Dropdown */
.dropbtn {
  background: none;
  color: #444;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  font-weight: 600;
}


.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropbtn:hover + .dropdown-content{
  display: block;
}

.dropbtn:focus + .dropdown-content{
  display: block;
}

.dropdown-content:hover{
  display: block;
}

.dropdown a:hover {background-color: #ddd;}

.show {display: block;}

